import axios from 'axios';
import {
  SearchConditionInterface,
  ResponsePageDataInterface,
} from './commonTypes';

export interface UserInterface {
  idEmail: String;
  // 用户姓名
  username: String;
  // 用户密码
  password: Number;
  // 用户标签
  tags: String[];
}
axios.defaults.withCredentials = true;
export class UserService {
  public static async getUsers(
    condition: SearchConditionInterface
  ): Promise<ResponsePageDataInterface<UserInterface>> {
    const resp = await axios.post('/api/login', condition);
    console.log(document.cookie);
    console.log(resp.headers['Set-Cookie']);
    return resp.data;
  }

  public static async clearUsers(
    condition: SearchConditionInterface
  ): Promise<void> {
    const resp = await axios.post('/api/login/out', condition);
    console.log(document.cookie);
    return resp.data;
  }
}
